<template lang="html">
  <div class="header">
    <h1>Formular za ocenjivanje kompetencija - Konačna Procena</h1>
    <h3>(1=Ne Zadovoljava Očekivanja)</h3>
    <h3>(2=Potrebno Poboljšanje)</h3>
    <h3>(3=Zadovoljava Očekivanja)</h3>
    <h3>(4=Iznad Očekivanja)</h3>
  </div>

  <div class="form-group">
    <label for="collegue">Podređeni za koga se vrši konačna procena:</label>
    <select name="collegue" id="collegue" v-model="podredjeniName" @change="onChangePodredjeni($event)">
       <option  v-for="podr in podredjeni" v-bind:key="podr.name">{{podr.name}}</option>
    </select>
    </div>

    <div class="form-group">
      <label for="date">Datum Procene:</label>
      <select name="date" id="collegue" v-model="CurrentDate" @change="onChangeDate($event)">
         <option  v-for="d in date" v-bind:key="d">{{d}}</option>
      </select>
      </div>

  <div class="kompetence" v-if="kompetenceProc!=0">
<h3>Kompetence</h3>
    <table id="kompetenceTable">
      <tr>
        <th>Kompetence :</th>
        <th>Ponder</th>
        <th>Samoprocena</th>
        <th>Konacna Procena</th>
        <th>Rangiranje</th>
      </tr>

      <tr v-for="(komp,i) in kompetence"  v-bind:key="i">
        <td>
          {{komp.name}}
          <button class="definicija"  type="button" name="button" @Click="swapBoolKompetence(i)">Definicija</button>
          <p v-if="kompetenceBool[i].bool" style="text-align: left;">
            <span style="white-space: pre-line">
            {{kompetenceBool[i].comment}}
            </span>
          </p>
        </td>
        <td>{{komp.ponder}}%</td>
        <td>{{komp.samoProcena}}</td>
        <td>{{komp.konacnaProcena}}</td>
        <td v-if="komp.rangiranje != null">{{komp.rangiranje.toFixed(2)}}</td>
        <td v-if="komp.rangiranje == null">0</td>
      </tr>

      <tr>
        <td colspan="4">Ukupno ocena:</td>
        <td >{{kompetenceOcena.toFixed(2)}}</td>
      </tr>
    </table>
</div>


<div class="licni" v-if="licniProc!=0">
  <h3>Licni KPI</h3>
  <table id="licniTable">
    <tr>
      <th>Kompetence :</th>
      <th>Ponder</th>
      <th>Samoprocena</th>
      <th>Konacna Procena</th>
      <th>Rangiranje</th>
    </tr>

    <tr v-for="(licni,j) in licniKpi"  v-bind:key="j">
      <td>
        {{licni.name}}
        <button class="definicija"  type="button" name="button" @Click="swapBoolLicni(j)">Definicija</button>
        <p v-if="licniBool[i].bool" style="text-align: left;">
          <span style="white-space: pre-line">
          {{licniBool[i].comment}}
          </span>
        </p>
      </td>
      <td>{{licni.ponder}}%</td>
      <td>{{licni.samoProcena}}</td>
      <td>{{licni.konacnaProcena}}</td>
      <td v-if="licni.rangiranje != null">{{licni.rangiranje.toFixed(2)}}</td>
      <td v-if="licni.rangiranje == null">0</td>
    </tr>

    <tr>
      <td colspan="4">Ukupno ocena:</td>
        <td >{{licniOcena.toFixed(2)}}</td>
    </tr>

  </table>

</div>

<div class="kompanijski" v-if="kompanijskiProc!=0">
  <h3>Kompanijski KPI</h3>
  <table id="kompanijskiTable">
    <tr>
      <th>Kompetence :</th>
      <th>Ponder</th>
      <th>Samoprocena</th>
      <th>Konacna Procena</th>
      <th>Rangiranje</th>
    </tr>

    <tr v-for="(kompa,k) in kompanijskiKpi"  v-bind:key="k">
      <td>
        {{kompa.name}}
        <button class="definicija"  type="button" name="button" @Click="swapBoolKompanijski(k)">Definicija</button>
        <p v-if="kompanijskiBool[i].bool" style="text-align: left;">
          <span style="white-space: pre-line">
          {{kompanijskiBool[i].comment}}
          </span>
        </p>
      </td>
      <td>{{kompa.ponder}}%</td>
      <td>{{kompa.samoProcena}}</td>
      <td>{{kompa.konacnaProcena}}</td>
      <td v-if="kompa.rangiranje != null">{{kompa.rangiranje.toFixed(2)}}</td>
      <td v-if="kompa.rangiranje == null">0</td>
    </tr>

    <tr>
      <td colspan="4">Ukupno ocena:</td>
      <td >{{kompanijskiOcena.toFixed(2)}}</td>
    </tr>
  </table>
</div>



  <div class="rezultat1" v-if="ukupno<0.75">
    <h3 class="ukupno">Ukupan učinak: {{ukupno.toFixed(2)}}</h3>
    <p>Rezultati loši, potrebno unapređenje za vršenje trenutne uloge ili prelazak na drugo radno mesto	</p>
  </div>
  <div class="rezultat2" v-if="ukupno>=0.75 && ukupno< 0.99">
    <h3 class="ukupno">Ukupan učinak: {{ukupno.toFixed(2)}}</h3>
    <p>Rezultati dobri, ali  neophodan razvoj za vršenje trenutne uloge
</p>
  </div>
  <div class="rezultat3" v-if="ukupno>=0.99 && ukupno< 1.17">
    <h3 class="ukupno">Ukupan učinak: {{ukupno.toFixed(2)}}</h3>
    <p>Apsolutno ostvarenje rezultata u trenutnoj ulozi
</p>

  </div>
  <div class="rezultat4" v-if="ukupno>=1.17">
    <h3 class="ukupno">Ukupan učinak: {{ukupno.toFixed(2)}}</h3>
    <p>Prebačaj rezultata u trenutnoj ulozi
</p>

  </div>

  <div class="komentar">
    <h4>Komentar Samoprocena</h4>
    <p  style="text-align: left;">
      <span style="white-space: pre-line">
      {{komentarSamoProcena}}
      </span>
    </p>
    <!-- <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentarSamoProcena"
      id="komentar"
      > -->
  </div>

  <div class="komentar">
    <h4>Komentar Konacna procena</h4>
    <p  style="text-align: left;">
      <span style="white-space: pre-line">
      {{komentar}}
      </span>
    </p>
    <!-- <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentar"
      id="komentar"
      > -->
  </div>


</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';

export default {
  computed: mapGetters(['user','users','companys','usersSameCompany']),
  data(){
    return{
      odgovori:[1,2,3,4],
      komentar:"",
      kompetence:[],
      licniKpi:[],
      kompanijskiKpi:[],
      ukupno:0,
      evaluatedUser:'',
      podredjeni:[],
      podredjeniName:'',
      podredjeniCeo:'',
      kompetenceOcena:0,
      licniOcena:0,
      kompanijskiOcena:0,
      kompanijskiProc:0,
      licniProc:0,
      kompetenceProc:0,
      kompetenceBool:[],
      kompanijskiBool:[],
      licniBool:[],
      lastEval:0,
      date:[],
      CurrentDate:"",
      komentarSamoProcena:"",
    }
  },
  methods:{
    ...mapActions(['getProfile','getCompanies','getCollegues']),
    onChangePodredjeni(event){
      this.date=[];
      for (var i = 0; i < this.usersSameCompany.length; i++) {
        if(this.usersSameCompany[i].name == event.target.value){
          this.podredjeniCeo=this.usersSameCompany[i];
        }
      }

      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation.length; i++) {
        this.date.push(this.podredjeniCeo.performanceEvaluation[i].date);
      }
    },
    onChangeDate(event){

      for (var i = 0; i < this.podredjeniCeo.performanceEvaluation.length; i++) {
        if(event.target.value==this.podredjeniCeo.performanceEvaluation[i].date){
          this.lastEval=i;
        }
      }

      this.komentarSamoProcena=this.podredjeniCeo.performanceEvaluation[this.lastEval].komentarSamoProcena;
      this.komentar=this.podredjeniCeo.performanceEvaluation[this.lastEval].komentar;

      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation[this.lastEval].kompetence.length; i++) {
        this.kompetence[i].samoProcena=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompetence[i].samoProcena;
        this.kompetence[i].konacnaProcena=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompetence[i].konacnaProcena;
        this.kompetence[i].rangiranje=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompetence[i].rangiranje;
      }
      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation[this.lastEval].kompanijski.length; i++) {
        this.kompanijskiKpi[i].samoProcena=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompanijski[i].samoProcena;
        this.kompanijskiKpi[i].konacnaProcena=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompanijski[i].konacnaProcena;
        this.kompanijskiKpi[i].rangiranje=this.podredjeniCeo.performanceEvaluation[this.lastEval].kompanijski[i].rangiranje;
      }
      this.licniKpi=[];
      for ( i = 0; i < this.podredjeniCeo.performanceEvaluation[this.lastEval].licniKpi.length; i++) {
              this.licniKpi.push(this.podredjeniCeo.performanceEvaluation[this.lastEval].licniKpi[i]);
      }
      this.updateTable();

    },
    swapBoolKompetence(i){
      if(this.kompetenceBool[i].bool){
        this.kompetenceBool[i].bool=false;
      }else{
        this.kompetenceBool[i].bool=true;
      }

    },
    swapBoolKompanijski(i){
      if(this.kompanijskiBool[i].bool){
        this.kompanijskiBool[i].bool=false;
      }else{
        this.kompanijskiBool[i].bool=true;
      }
    },
    swapBoolLicni(i){
      if(this.licniBool[i].bool){
        this.licniBool[i].bool=false;
      }else{
        this.licniBool[i].bool=true;
      }
    },
    updateTable(){
      //kompetence
      let tempCompany={};
      for ( i = 0; i < this.companys.length; i++) {
        if(this.companys[i].name == this.podredjeniCeo.type){
          tempCompany=this.companys[i];
        }
      }

      this.lastEval=this.podredjeniCeo.performanceEvaluation.length-1;

      let brojac = tempCompany.performanceEvaluation.kompanijski.length;
      for ( i = 0; i < brojac; i++) {
        let komp={
          bool:false,
          comment:tempCompany.performanceEvaluation.kompanijski[i].comment
        }
        this.kompanijskiBool.push(komp);
      }

      brojac = tempCompany.performanceEvaluation.kompetence.length;
      for ( i = 0; i < brojac; i++) {
        let komp={
          bool:false,
          comment:tempCompany.performanceEvaluation.kompetence[i].comment
        }
        this.kompetenceBool.push(komp);
      }

      brojac = this.podredjeniCeo.performanceEvaluation[this.lastEval].licniKpi.length;
      for ( i = 0; i < brojac; i++) {
        let komp={
          bool:false,
          comment:this.podredjeniCeo.performanceEvaluation[this.lastEval].licniKpi[i].comment
        }
        this.licniBool.push(komp);
      }



      let ukupnoKompetence = 0;
      for (var i = 0; i < this.kompetence.length; i++) {
        if(this.kompetence[i].konacnaProcena!=null){
          this.kompetence[i].rangiranje=((this.kompetence[i].konacnaProcena*0.25 + 0.25)*this.kompetence[i].ponder)/100;
          ukupnoKompetence+=this.kompetence[i].rangiranje;
        }
      }
      this.kompetenceOcena= ukupnoKompetence;
      //licni
      let ukupnoLicni=0;
      for ( i = 0; i < this.licniKpi.length; i++) {
        if(this.licniKpi[i].konacnaProcena!=null){
          this.licniKpi[i].rangiranje=((this.licniKpi[i].konacnaProcena*0.25 + 0.25)*this.licniKpi[i].ponder)/100;
          ukupnoLicni+=this.licniKpi[i].rangiranje;
        }
      }
      this.licniOcena=ukupnoLicni;
      //kompanijski
      let ukupnoKompanijski=0;
      for ( i = 0; i < this.kompanijskiKpi.length; i++) {
        if(this.kompanijskiKpi[i].konacnaProcena!=null){
          this.kompanijskiKpi[i].rangiranje=((this.kompanijskiKpi[i].konacnaProcena*0.25 + 0.25)*this.kompanijskiKpi[i].ponder)/100;
          ukupnoKompanijski+= this.kompanijskiKpi[i].rangiranje;
        }
      }
      this.kompanijskiOcena=ukupnoKompanijski;

      this.ukupno = tempCompany.performanceEvaluation.kompetenceProcenat/100 * ukupnoKompetence + tempCompany.performanceEvaluation.licniProcenat/100 * ukupnoLicni + tempCompany.performanceEvaluation.kompanijskiProcenat/100*ukupnoKompanijski;

    },

  },
  async created(){

    await this.getProfile();
    await this.getCompanies();
    await this.getCollegues(this.user.type);

    let thisCompany;
    for (var i = 0; i < this.companys.length; i++) {
      if(this.user.type==this.companys[i].name){
        thisCompany=this.companys[i];
      }
    }

    let brojac = thisCompany.performanceEvaluation.kompanijski.length;
    for ( i = 0; i < brojac; i++) {
      let komp={
        bool:false,
        comment:thisCompany.performanceEvaluation.kompanijski[i].comment
      }
      this.kompanijskiBool.push(komp);
    }

    brojac = thisCompany.performanceEvaluation.kompetence.length;
    for ( i = 0; i < brojac; i++) {
      let komp={
        bool:false,
        comment:thisCompany.performanceEvaluation.kompetence[i].comment
      }
      this.kompetenceBool.push(komp);
    }

    this.kompetenceProc=thisCompany.performanceEvaluation.kompetenceProcenat;
    this.licniProc=thisCompany.performanceEvaluation.licniProcenat;
    this.kompanijskiProc=thisCompany.performanceEvaluation.kompanijskiProcenat;

    for ( i = 0; i < thisCompany.performanceEvaluation.kompetence.length; i++) {
      this.kompetence.push(thisCompany.performanceEvaluation.kompetence[i]);
    }

    for ( i = 0; i < thisCompany.performanceEvaluation.kompanijski.length; i++) {
      this.kompanijskiKpi.push(thisCompany.performanceEvaluation.kompanijski[i]);
    }

    for ( i = 0; i < this.user.performanceEvaluationPodredjeni.length; i++) {
      for (var j = 0; j < this.usersSameCompany.length; j++) {
        if(this.user.performanceEvaluationPodredjeni[i]==this.usersSameCompany[j]._id){
          this.podredjeni.push(this.usersSameCompany[j]);
        }
      }
    }

  },

  beforeCreate: function() {
        document.body.className = 'other';
    }

  }

</script>

<style lang="css" scoped>
.rezultat1{
  background-color: red;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.rezultat2{
  background-color: yellow;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.rezultat3{
  background-color: cyan;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.rezultat4{
  background-color: green;
  color: white;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
  padding:10px;
  text-align: center;
  border-top: 0px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
p{

}
.ukupno{
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
table, th, td{
  border: 1px solid black;
  text-align: center;
}
tr:nth-child(even){background-color: #bbbbbb;}
tr:nth-child(odd){background-color: #ffffff;}
tr:hover {background-color: #afa;}
table{
   margin: auto;
}
.header{
  background-color: #ffaaaa;
  padding: 15px;
  border-style: solid;
  border-color: black;
